.MenuCard {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

}

.MenuCard_Title2 {
    position: absolute;
    top: -1.3rem;
    height: 45px;
    width: 50%;
    z-index: 999;
    background-color: rgb(76, 0, 255);
    clip-path: polygon(10% 0, 90% 0, 100% 50%, 90% 100%, 10% 100%, 0% 50%);
    /* box-shadow: rgba(240, 46, 170, 0.4) 0px 5px, rgba(240, 46, 170, 0.3) 0px 10px, rgba(240, 46, 170, 0.2) 0px 15px, rgba(240, 46, 170, 0.1) 0px 20px, rgba(240, 46, 170, 0.05) 0px 25px; */
}

.MenuCard_Title2 span {

    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(5, 4, 4, 0.839);
    width: calc(100%-6px);
    height: calc(100%-6px);
    clip-path: polygon(10% 0%, 90% 0, 99% 50%, 90% 100%, 10% 100%, 1% 50%);
    top: 3px;
    left: 3px;
    right: 3px;
    bottom: 3px;
}

.MenuCard_Title_text2 {
    color: skyblue;
    font-weight: 600;
    font-size: 1.8rem;
    letter-spacing: 0.4rem;
    /* text-shadow: blueviolet; */
    text-shadow: skyblue 1px 0 6px;
}

.MenuCard_container2 {
    width: 100%;
    height: 100%;
    padding: 0;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(76, 0, 255);
    clip-path: polygon(2% 0%, 98% 0%, 100% 2%, 100% 98%, 98% 100%, 2% 100%, 0% 98%, 0% 3%);
    /* box-shadow: 0px 0 30px 5px #245aae inset; */
}

.MenuCard_container2_child {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100%-6px);
    height: calc(100%-6px);
    top: 3px;
    left: 3px;
    right: 3px;
    bottom: 3px;
    border: none;
    /* background-color: rgba(5, 4, 4, 0.839); */
    background-color: #01091d;

    clip-path: polygon(2% 0%, 98% 0%, 100% 2%, 100% 98%, 98% 100%, 2% 100%, 0% 98%, 0% 3%);
    /* box-shadow: 0 0 30px 5px #245aae inset; */
}

.Modalclose {
    border: 3px solid rgb(76, 0, 255);
    top: -3px;
    border-radius: 0 0 0 2px;
    right: -3px;
    /* text-align: left; */
    /* padding-left: 1.2rem; */
    z-index: 999;
    background: rgba(5, 4, 4, 0.839);
}

.Modalclose:hover {
    background: rgb(55, 41, 41);
}

.MenuCard_Content2 {
    color: skyblue;
    font-size: 1rem;
    width: 100%;
    max-width: 100%;
    height: 100%;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 0.5rem;
    transform-origin: center center;
    box-sizing: border-box;
    transition: all 0.3s ease-in-out;
}

.MenuCard2_btn {
    position: absolute;
    top: 1.2rem;
    left: 1.5rem;
    display: grid;
    grid-template-columns: auto auto;
    gap: 1rem;
    cursor: pointer;
    z-index: 999;

}

.MenuCard2_btn .btnChoose {
    height: 2rem;
    width: 6rem;
    position: relative;
    clip-path: polygon(15% 0%, 100% 0, 100% 15%, 100% 75%, 85% 100%, 0 100%, 0 100%, 0% 25%);
    background: rgba(0, 0, 255, 0.812);
    padding: 0.7rem 2.5rem;




}

.MenuCard2_btn .btnChoose span {
    content: '';
    position: absolute;
    height: calc(100%-4px);
    width: calc(100%-4px);
    top: 2px;
    left: 2px;
    right: 2px;
    bottom: 2px;
    background: #000;
    color: skyblue;
    font-weight: 600;
    font-size: 1rem;
    clip-path: polygon(15% 0%, 100% 0, 100% 15%, 100% 75%, 85% 100%, 0 100%, 0 100%, 0% 25%);



}

.MenuCard2_btn .btnChoose:active {
    transform: translate(0%, 4%);
}

.DetaiMachineModal_Content {
    position: absolute;
    bottom: 0.4rem;
    height: calc(100% - 3rem);
    width: 100%;
    /* border: 1px solid rgb(55, 252, 0); */
}


.fontsize-chart2 {
    font-size: 0.65rem;


}

.fontsize-chart3 {
    font-size: 0.6rem;

}

.btnChooseAction {
    box-shadow: 0 0 1em .25em rgb(25, 1, 47),
        0 0 4em 1em rgba(73, 7, 134, 0.781),
        inset 0 0 .75em .25em rgb(47, 37, 56);
    /* filter: blur(15px); */
    background-color: rgb(19, 6, 84) !important;
}


.textShadowSkyBlue {
    text-shadow: rgb(25, 32, 238) 1px 0 4px;
    letter-spacing: 0.15rem;
}


.thead {

    top: 0;
    box-shadow: 0 -2px 0 0 #3a3176;
    /* Tạo viền dưới cho thead */
    z-index: 1;

}

.theadtr {
    top: 0;
    box-shadow: 0 1px 0 0px #3a3176;
    /* Tạo viền dưới cho thead */
    z-index: 1;

}