html {
    background-color: black;
    overflow: hidden;
}

.bgMain {
    /* background: url('../assets/img/bg.gif'); */
    background: url('../assets/bg/4.jfif');
    /* background: url('../assets/bg/2.jpg'); */
   
    background-position: center;
    /* background-repeat: no-repeat;
    background-size: cover; */
    /* background-color: rgb(0, 0, 0); */
    /* background: rgba(9, 8, 9, 0.501); */
}

.Main_header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    height: 60px;
}

.Main_title {
    position: relative;
    width: 50%;
    height: 60px;
    background: rgba(0, 0, 255, 0.812);
    clip-path: polygon(0 0, 100% 0, 86% 100%, 14% 100%);
}

.Main_title .sub_Main_title {
    position: absolute;
    top: 3px;
    left: 3px;
    right: 3px;
    bottom: 3px;
    width: calc(100% - 6px);
    height: calc(100% - 6px);
    clip-path: polygon(1.5% 0%, 98.5% 0, 86% 100%, 14% 100%);
    background: rgba(5, 6, 7, 0.863);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    color: skyblue;
    font-weight: 500;
    letter-spacing: 0.2rem;
    /* text-shadow: blueviolet; */
    /* text-shadow: blueviolet 1px 0 6px; */
    text-shadow: rgb(159, 86, 228) 2px 0 6px;
    box-shadow: 10px 0 20px 0px #245aae inset;
    /* box-shadow: 10px 0 22px 2px #245aae inset; */

}

.Main_left_title {
    clip-path: polygon(0 0, 78% 0, 97% 100%, 0% 100%);
    width: 31%;
    height: 55px;
    top: 0;
    background: rgba(33, 23, 47, 0.755);

}


.Main_left_title .sub_Main_left_title {
    position: absolute;
    top: 3px;
    left: 3px;
    right: 3px;
    bottom: 3px;
    background: rgba(0, 0, 0, 0.765);
    
    display: flex;
    justify-content: center;
    align-items: center;
    clip-path: polygon(0% 0, 78.2% 0, 95% 100%, 0% 100%);
    height: calc(100% -6px);
    width: calc(100% -6px);
    /* box-shadow: 0px 0 10px 2px #245aae inset; */

}

.Main_right_title {
    clip-path: polygon(22.5% 0, 100% 0, 100% 100%, 3% 100%);
    width: 31%;
    height: 55px;
    top: 0;
    /* background: rgba(23, 0, 56, 0.93); */
    background: rgba(33, 23, 47, 0.755);
}

.Main_right_title .sub_Main_right_title {
    clip-path: polygon(22.5% 0%, 100% 0, 100% 100%, 5% 100%);
    position: absolute;
    top: 3px;
    left: 3px;
    right: 3px;
    bottom: 3px;
    background: rgba(0, 0, 0, 0.765);
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100% -6px);
    width: calc(100% -6px);
}


.Main_content {
    margin-top: 10px;
    height: calc(100% - 90px);
    width: 100%;
}

.MenuCard_container {
    height: 100%;
    width: 100%;
    border: 3px solid rgba(0, 0, 255, 0.812);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #1031664a; */
    background-color: #01173b25;
    box-shadow: 0 0 30px 3px #245aae inset;

}

.MenuCard_Title {
    position: absolute;
    top: -1.15rem;
    height: 35px;
    width: 50%;
    background-color: rgb(76, 0, 255);
    clip-path: polygon(10% 0, 90% 0, 100% 50%, 90% 100%, 10% 100%, 0% 50%);
    box-shadow: rgba(240, 46, 170, 0.4) 0px 5px, rgba(240, 46, 170, 0.3) 0px 10px, rgba(240, 46, 170, 0.2) 0px 15px, rgba(240, 46, 170, 0.1) 0px 20px, rgba(240, 46, 170, 0.05) 0px 25px;
}

.MenuCard_Title span {

    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(5, 4, 4, 0.839);
    width: calc(100%-6px);
    height: calc(100%-6px);
    clip-path: polygon(10% 0%, 90% 0, 99% 50%, 90% 100%, 10% 100%, 1% 50%);
    /* filter:drop-shadow(1px 0px 10px rgb(141, 183, 200)); */

    top: 3px;
    left: 3px;
    right: 3px;
    bottom: 3px;
}

.MenuCard_Title_text {
    color: skyblue;
    font-weight: 500;
    font-size: 1.0rem;
    letter-spacing: 0.2rem;
    text-shadow: blueviolet;
    text-shadow: rgb(148, 73, 217) 1px 0 6px;

}

.MenuCard_Content {
    color: skyblue;
    font-size: 1rem;
}

.MenuCard_Content {
    width: 100%;
    max-width: 100%;
    height: 100%;
    border: 1px solid;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 1.1rem;
    padding-bottom: 0.3rem;
    transform-origin: center center;
    box-sizing: border-box;
    transition: all 0.3s ease-in-out;
}




