.cube {
    position: absolute;
    bottom: 15%;
    left: calc(50% - 4rem);
    width: 11rem;
    height: 7rem;
    transform-style: preserve-3d;
    /* animation: animate 20s linear infinite; */
}

.subCube {
    transform: rotateX(-16deg) rotateY(300deg);

}

@keyframes animate {
    0% {
        transform: rotateX(-16deg) rotateY(0deg);
    }

    100% {
        transform: rotateX(-16deg) rotateY(-360deg);
    }
}

.cube .edge {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
}

.cube .edge span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background: linear-gradient(#151515, #0c0cff, #0c0cff, #0c0cff, #080808); */
    background: linear-gradient(#151515, #0000d6, #0000d6, #0000d6, #5c73f651);
    opacity: 0.75;
    transform: rotateY(calc(60deg * var(--i))) translateZ(150px);
}

.LogoFactory {
    filter: grayscale(80%) drop-shadow(0 0 0.75rem rgb(173, 65, 166));
    opacity: 1;
    height: 58%;
    user-select: none;
}

.cubeTop {
    position: absolute;
    top: -9.0rem;
    left: calc(50% - 10.6rem);
    height: 18.3rem;
    width: 21.3rem;
    background: rgba(3, 58, 130, 0.863);
    transform: rotateZ(0deg) rotate3d(1, 0, 0, 90deg);
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);

}

.cubeTop span {

    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: calc(18.3rem - 3.3rem);
    width: calc(21.3rem - 4.3rem);
    background: rgb(198, 208, 234);
    /* box-shadow: inset 0px 0px 50px 30px rgba(228, 24, 24, 0.1); */
    box-shadow: inset 0px 0px 46px 49px rgba(61, 177, 185, 0.226);

    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);

}

.cubeBottom {

    position: absolute;
    /* box-shadow: 0px 0px 50px 85px rgba(170, 54, 54, 0.671); */
    top: -2.3rem;
    left: calc(50% - 10.8rem);
    height: 18.3rem;
    width: 21.3rem;
    background: rgba(53, 44, 60, 0.993);
    /* background: rgba(229, 220, 236, 0.993); */
    transform: rotateZ(0) rotate3d(1, 0, 0, 90deg);
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
}



@keyframes counter-animate {
    0% {
        transform: rotateY(0deg);
    }

    100% {
        transform: rotateY(360deg);
    }
}

@keyframes counter-animate3 {
    0% {
        transform: rotateY(0deg);
    }

    100% {
        transform: rotateY(-360deg);
    }
}

@keyframes counter-animate2 {

    0%,
    100% {
        transform: translateY(0rem);

    }

    50% {
        transform: translateY(3rem);


    }
}

