.cubeActive .MapArea {
    position: absolute;
    height: 25rem;
    width: 25rem;
    bottom: 8rem;
    padding: 0 2rem;
    left: calc(50% - 12.8rem);
    animation: counter-animate 20s linear infinite;

    /* animation: counter-animate 20s linear infinite, counter-animate2 5s ease-in-out infinite; */
}


.cube .mapSVG {
    fill: rgba(56, 0, 223, 0.229);
    stroke-width: 4px;
    stroke: rgb(207, 179, 236);
    z-index: 1;
    filter: drop-shadow(0.2rem 0.2rem 0.7rem rgb(73, 94, 123));


}

.cubeActive .mapSVG {
    fill: rgba(231, 93, 28, 0.253);
    stroke-width: 4px;
    stroke: rgb(234, 163, 72);
    z-index: 1;
    filter: drop-shadow(0.2rem 0.2rem 0.75rem rgb(251, 217, 175));


}

.brightArea {
    position: absolute;
    height: 27rem;
    width: 25.3rem;
    bottom: 0rem;
    left: calc(50% - 12.8rem);
    background: linear-gradient(rgba(195, 80, 240, 0), #0d0de641, #7777da36, #d6cbcb2c, #f8f8f800);
    clip-path: polygon(0 0, 100% 0, 68% 100%, 32% 100%);
    /* clip-path: polygon(14% 7%, 27% 0, 39% 8%, 49% 0, 61% 9%, 72% 0, 85% 6%, 100% 0, 68% 100%, 32% 100%, 0 0, 0 0); */
    /* animation: counter-animate 20s linear infinite; */
    transform: rotateX(0deg) rotateY(240deg);

}



.brightAreaActive {
    position: absolute;
    height: 27rem;
    width: 25.3rem;
    bottom: 0rem;
    left: calc(50% - 12.8rem);
    background: linear-gradient(rgba(195, 80, 240, 0), #e6760d41, #dab27736, #d6cbcb2c, #f8f8f800);
    clip-path: polygon(0 0, 100% 0, 68% 100%, 32% 100%);
    animation: counter-animate 20s linear infinite;

    /* clip-path: polygon(14% 7%, 27% 0, 39% 8%, 49% 0, 61% 9%, 72% 0, 85% 6%, 100% 0, 68% 100%, 32% 100%, 0 0, 0 0); */
}

.mapSVGMark {
    fill: rgba(206, 0, 0, 0.253);
    stroke-width: 4px;

    /* stroke: rgb(248, 17, 1); */
    z-index: 1;
    animation: changeColor 2s linear infinite;

}



@keyframes changeColor {

    0%,
    100% {
        stroke: rgba(206, 0, 0, 0.927);
        fill: rgba(195, 25, 25, 0.416);


    }

    50% {
        stroke: rgba(186, 198, 18, 0.846);
        fill: rgba(186, 198, 18, 0.431);



    }
}





.ChooseFac_btn {
    position: absolute;
    bottom: 1.2rem;
    left: 50%;
    transform: translate(-50%, -50%);
    display: grid;
    grid-template-columns: auto auto;
    gap: 1rem;
    cursor: pointer;
    z-index: 999;

}

.ChooseFac_btn .btnChoose {
    height: 3rem;
    width: 10rem;
    position: relative;

    clip-path: polygon(15% 0%, 100% 0, 100% 15%, 100% 75%, 85% 100%, 0 100%, 0 100%, 0% 25%);
    background: rgba(0, 0, 255, 0.812);
    padding: 0.7rem 2.5rem;




}

.ChooseFac_btn .btnChoose span {
    content: '';
    position: absolute;
    height: calc(100%-4px);
    width: calc(100%-4px);
    top: 2px;
    left: 2px;
    right: 2px;
    bottom: 2px;
    background: #000;
    color: skyblue;
    font-weight: 700;
    letter-spacing: 0.1rem;
    font-size: 1.4rem;
    display: flex;
    justify-content: center;
    align-items: center;

    clip-path: polygon(15% 0%, 100% 0, 100% 15%, 100% 75%, 85% 100%, 0 100%, 0 100%, 0% 25%);



}

.ChooseFac_btn .btnChoose:active {
    transform: translate(0%, 4%);
}



.cubeActive {

    animation: animate 20s linear infinite;

}

