.img_humidifier_box {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.img_humidifierTotal {
  width: 70%;
  height: auto;
  opacity: 0.8;
  filter: drop-shadow(0 0 4px rgb(255, 255, 255));
}
.img_humidifierOn {
  width: 70%;
  height: auto;
  opacity: 0.8;
  filter: drop-shadow(0 0 4px rgb(41, 248, 0));
}
.img_humidifierOff {
  width: 70%;
  height: auto;
  opacity: 0.8;
  filter: drop-shadow(0 0 4px rgb(247, 30, 1));
}
